import _ from 'lodash';
import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

// import images
import SecurityImages from 'assets/images/Security';

// import subscribe section
import SubscribeSection from '../Categories/SubscribeSection';

// Footer
import FooterContainer from '../../containers/FooterContainer';

import {
  onPageInsurance,
  setStateInsurance,
  onPageMore,
  setStateMore,
  onPageBusiness,
  setStateBusiness,
} from '../../actions/navActions';

import './Security.scss';

class Security extends Component {
  constructor(props) {
    super(props);

    // reset indication
    const resetInsurance = onPageInsurance.NONE;
    const resetMore = onPageMore.NONE;
    const resetBusiness = onPageBusiness.NONE;
    const {
      resetIndicatorInsurance,
      resetIndicatorMore,
      resetIndicatorBusiness,
    } = this.props;
    resetIndicatorInsurance(resetInsurance);
    resetIndicatorMore(resetMore);
    resetIndicatorBusiness(resetBusiness);

    this.icons = [
      { title: 'Bank Level Standard Security', src: SecurityImages.Bank, desc: 'PolicyPal uses HTTP/ SSL to encrypt communications between front-end (web/mobile apps) to the back-end services (RESTful), and prevent man-in-the-middle attacks/eavesdropping and the likes.' },
      { title: 'Singapore Government\'s PDPA Compliant', src: SecurityImages.HandShake, desc: 'PolicyPal places great emphasis on protecting your privacy and collects, processes and utilises your personal information only in accordance with this Privacy Policy for Singapore and the requirements of the PDPA. We do not share your data without your consent.' },
      { title: 'Secure Authentification', src: SecurityImages.Shield, desc: 'PolicyPal utilises token-based authentication/ Oauth2 for authentication and authorisation. This ensures that we can safely control the permissions to access specific resources through our RESTful services, and specify the desirable duration for each token.' },
    ];
  }
  renderIcons() {
    return (
      _.map(this.icons, (icon, index) => (
        <div key={index} className="icon-container">
          <div className="animate">
            <img src={icon.src} alt={`icon-${icon.title}`} />
          </div>
          <div className="desc">
            {icon.title}
          </div>
          <div className="sub-desc">
            {icon.desc}
          </div>
        </div>
      ))
    );
  }
  render() {
    window.scroll(0, 0);
    return (
      <div className="security">
        <div className="title-container">
          <div className="title">Your Security is Our Top Priority</div>
          <div className="desc">
            Thousands of individual trust us with the security of their policies information.
            <br />We take your trust seriously and strive to improve our cyber security measures daily.
          </div>
        </div>
        <div className="content">
          <div className="content-row grey-bg">
            <div className="title">
              Your Data is 100% Safe With Us
              <div className="icon">
                {this.renderIcons()}
              </div>
            </div>
          </div>
        </div>
        <SubscribeSection />
        <FooterContainer />
      </div>
    );
  }
}

Security.defaultProps = {
  resetIndicatorInsurance: null,
  resetIndicatorMore: null,
  resetIndicatorBusiness: null,
};
Security.propTypes = {
  resetIndicatorInsurance: PropTypes.func,
  resetIndicatorMore: PropTypes.func,
  resetIndicatorBusiness: PropTypes.func,
};

const mapDispatchToProps = dispatch => ({
  resetIndicatorInsurance: (resetInsurance) => {
    dispatch(setStateInsurance(resetInsurance));
  },
  resetIndicatorMore: (resetMore) => {
    dispatch(setStateMore(resetMore));
  },
  resetIndicatorBusiness: (resetBusiness) => {
    dispatch(setStateBusiness(resetBusiness));
  },
});

export default connect(null, mapDispatchToProps)(Security);